.wrapper-about {
  display: flex;
  max-width: 1440px;
  margin-right: auto;
  margin-left: auto;
  background: white;
  padding-bottom: 100px;
}
.box-about-left {
  flex-basis: 527px;
  flex-grow: 1;
}
.box-about-left .title-box {
  margin-top: 100px;
  margin-left: 140px;
  max-width: 700px;

}
.small-description {
  display: flex;
  justify-content: start;
  align-items: center;
}
.small-title {
  margin-left: 15px;
  font-family: @fontRegular;
  font-size: 14px;
  color: @black;
}
.box-about-left h2 {
  max-width: 418px;
}

.about-desc {
  margin-left: 140px;
  margin-top: 20px;
}

.about-desc p {
  max-width: 600px;
  margin-top: 27px;
}
.about-product {
  margin-top: 53px;
}
.about-product .product-items {
  margin-top: 43px;
  display: flex;
}
.box-icon-product {
  display: flex;
  justify-content: start;
  align-items: center;
}
.box-icon-product img{
  flex-shrink: 0;
  margin-right: 18px;
}
.product-item-1 {
  margin-right: 15px;
}
.product-item-1 p {
  width: 300px;
}
.box-about-right {
  flex-grow: 2;
  display: flex;
  width: 100%;
  justify-content: end;
  align-items: flex-end;

}
.foto {
}
.red-cub-foto {
  width: 428px;
  background-color: @red;
  height: 500px;
  margin-left: 50px;
  position: relative;
}

.tz-foto {
  position: absolute;
  bottom: 0;
  left: 60px;
}

















