.wrapper-product-slider {
  max-width: 1440px;
  margin-right: auto;
  margin-left: auto;
  margin-top: 60px;
  height: 750px;
  background-color: @black;
  position: relative;
  padding-top: 98px;
}
.slider-product-items {
  display: flex;
  overflow: hidden;
  margin-left: 140px;
}
.slider-product-item {
  margin-right: 30px;
}

.slider-product-item span {
  color: @red;
  font-family: @fontRegular;
  float: left;
  margin-right: 10px;
}
.wrapper-product-slider h2{
  margin-left: 140px;
  color: white;
  width: 700px;
}

.img-slider-product {
  width: 300px;
  height: 500px;
  background-color: white;
}
.slider-product-title {
  color: white;
}