}

.slider-product-title {
  color: white;
}

.wrapp-project {
  max-width: 1440px;
  margin-right: auto;
  margin-left: auto;
  margin-top: 70px;
  padding-bottom: 100px;
  background: white;
}

.wrapp-project .title-box {
  margin-left: 140px;
}

.projects-all-wrapper {
  display: flex;
}

.project-box1 {
  margin-right: 70px;
  margin-left: 140px;
}

.img-project-main {
  width: 400px;
  height: 596px;
  background-color: crimson;
  margin-bottom: 19px;
}

.project-main-title {
  margin-top: 20px;
}

.project-box-title__main {
  display: flex;
  align-items: center;
}

.project-box-title.project-box-title__main h5 {
  margin-top: 0;
  margin-bottom: 0;
  max-width: 300px;
}

.project-box2 {
  display: flex;
  flex-direction: column;
  justify-content: start;
}

.common-project-small-wrapp {
  display: flex;
  flex-wrap: wrap;
  background-color: yellow;
  max-width: 650px;
  margin: 0 auto;
}

.wrapper-project-small {
  flex: 1 1 calc(50% - 50px);
