  margin-bottom: 20px;
}

.navigation-bar {
  margin-top: 30px;
  display: flex;
  align-items: center;
}

.navigation {
  width: 300px;
  height: 100px;
  align-self: center;
}

.btn.btn-border {
  align-self: flex-end;
  background: transparent;
  border: 1px solid #FF0000;
  color: #FF0000;
  margin-right: 25px;
  margin-left: auto;
}

.wrap-consultation {
  max-width: 1440px;
  margin-right: auto;
  margin-left: auto;
  margin-top: 70px;
  padding-bottom: 100px;
  display: flex;
}

.box-consultation-1 .title-box {
  margin-left: 140px;
  max-width: 332px;
  margin-right: 126px;
  padding-top: 105px;
}

.box-consultation-2 {
  width: 100%;
  padding-left: 125px;
  padding-top: 105px;
  background-color: #F2F2F2;
}

.consultation-title {
  margin-bottom: 40px;
}

form {
  margin-top: 140px;
}

form div {
  margin-bottom: 30px;
}

*:focus {
  outline: none;
}

input {
  width: 400px;
  height: 60px;
  border: 0;
  border-bottom: 1px solid #3F3F3F;
  background-color: transparent;
}

input::placeholder {
  color: #FF0000;
  margin-bottom: 20px;
  font-family: 'Raleway Regular';
  font-size: 16px;
}

.checkbox {
  width: 16px;
  height: 16px;
  border: 1px solid #b5b4b4;
  float: left;
  margin-right: 13px;
  margin-top: 6px;
}

input:checked + p {
  color: #B5B4B4;
}

.btn.btn-button {
  margin-top: 45px;
  text-decoration: none;
  border: 0;
  width: 193px;
  margin-bottom: 80px;
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.shadow {
  max-width: 1440px;
  margin-right: auto;
  margin-left: auto;
  box-shadow: 0 1em 20px 0 rgba(122, 122, 122, 0.27);
}

.cube-active {
  background: #FF0000;
  width: 9px;
  height: 9px;
  display: block;
  float: left;
}

a {
  text-decoration: none;
}

h1 {
  font-family: 'Raleway Black';
  font-weight: 900;
  font-size: 60px;
  color: #303030;
  width: 362px;
  margin-top: 48px;
  margin-bottom: 46px;
  line-height: 110.4%;
}

h2 {
  font-family: 'Raleway Black';
  font-size: 48px;
  line-height: 110.4%;
  color: #303030;
  width: 400px;
  margin-top: 30px;
  margin-bottom: 50px;
}

h3 {
  font-family: 'Raleway Bold';
  font-size: 26px;
  color: #303030;
}

h4 {
  font-family: 'Raleway Black';
  font-size: 14px;
  color: #303030;
}

header {
  display: flex;
  max-width: 1440px;
  margin: auto;
}

.menu {
  display: flex;
}

.link-menu {
  display: flex;
  margin-left: 70px;
  align-items: center;
  justify-content: space-between;
  width: 362px;
}

.link-item a {
  font-family: 'Raleway Regular';
  font-size: 14px;
  color: #787777;
}

.link-item a.active-link {
  color: #303030;
}

.cube-active__menu {
  margin-right: 14px;
  margin-top: 4px;
}

.logo {
  width: 82px;
  height: 82px;
  background: #3F3F3F;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wrap-offer {
  margin-left: 140px;
}

.slogan {
  font-family: 'Raleway Regular';
  font-size: 20px;
  line-height: 23px;
  margin-bottom: 23px;
}

.btn {
  width: 150px;
  height: 54px;
  background: #FF0000;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  text-decoration: none;
}

.wrap-img {
  max-width: 636px;
  margin-top: 66px;
  height: 226px;
  width: 100%;
  overflow: hidden;
  position: relative;
}

.line {
  height: 180px;
  display: inline-flex;
  overflow: hidden;
  transition: all ease 1s;
  position: absolute;
  right: 0;
  left: auto;
  padding-bottom: 100px;
}

.line .img-gallary {
  margin-right: 18px;
  width: 215px;
  height: 211px;
}

.line .im-small-active {
  box-shadow: 1px 5px 11px 0 #3f3f3f8a;
}

.box-1 {
  margin-right: 51px;
  flex-basis: 603px;
}

.box-2 {
  width: 100%;
  max-width: 655px;
  display: inline-flex;
  overflow: hidden;
  flex-wrap: nowrap;
}

.box-2 .img-maim {
  height: 750px;
  flex-shrink: 0;
  display: none;
}

.box-2 .im-main-active {
  display: block;
  transition: all ease 1s;
}

.box2-wrapp {
  position: relative;
  height: 750px;
  width: 100%;
}

.arrows {
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 10;
}

.arrow-left {
  display: flex;
  align-content: normal;
  justify-content: center;
  align-items: center;
  background: #FF0000;
  width: 97px;
  height: 97px;
  float: left;
}

.arrow-right {
  display: flex;
  align-content: center;
  justify-content: center;
  align-items: center;
  background: #fff;
  width: 97px;
  height: 97px;
}

.arrows img {
  height: auto;
}

.box-3 {
  display: flex;
  width: 100px;
  flex-direction: column;
  justify-content: center;
  flex-grow: 1;
  flex-shrink: 0;
}

.burger {
  margin-top: 15px;
  width: 100%;
  max-width: 37px;
  align-self: center;
  flex-grow: 2;
}

.burger-item {
  background: #3F3F3F;
  height: 2px;
  display: block;
  margin-bottom: 7px;
}

.burger-item__1 {
  width: 31px;
}

.burger-item__2 {
  width: 24px;
}

.burger-item__3 {
  width: 37px;
}

.social {
  align-self: center;
  width: 20px;
  flex-grow: 3;
}

.social img {
  margin-bottom: 33px;
}

@media screen and (max-width: 1370px) {
  .logo {
    width: 78px;
    height: 78px;
    flex-shrink: 0;
  }

  .box-2 .img-maim {
    width: 100%;
  }

  .arrows img {
    width: auto;
    height: auto;
  }

  .box-3 {
    width: 70px;
    flex-shrink: 0;
  }
}

@media screen and (max-width: 1080px) {
  h1 {
    font-size: 36px;
    width: 200px;
  }

  header {
    box-shadow: none;
  }

  .link-menu {
    width: 250px;
  }

  .link-item__2 {
    display: none;
  }

  .line .img-gallary {
    margin-right: 10px;
    width: 109px;
    height: 107px;
  }

  .box-1 {
    flex-basis: 326px;
  }

  .box-2 .img-maim {
    height: 586px;
  }

  .box2-wrapp {
    height: 586px;
  }
}

@media screen and (max-width: 992px) {
  .box2-wrapp {
    height: 586px;
  }
}

@media screen and (max-width: 768px) {
  h1 {
    font-size: 36px;
    width: 222px;
  }

  .link-menu {
    width: 162px;
    margin-left: 42px;
  }

  .logo {
    width: 55px;
    height: 55px;
  }

  .wrap-offer {
    margin-left: 93px;
  }

  .arrow-left {
    width: 49px;
    height: 49px;
  }

  .arrow-right {
    width: 49px;
    height: 49px;
  }

  .arrows img {
    width: auto;
    height: auto;
  }
}

@media screen and (max-width: 576px) {
  h1 {
    font-size: 32px;
    width: 215px;
    margin-top: 40px;
    margin-bottom: 32px;
    line-height: 110.4%;
  }

  .link-menu {
    display: none;
  }

  .logo {
    width: 46px;
    height: 46px;
  }

  .logo img {
    width: 14px;
    height: 14px;
  }

  .wrap-offer {
    margin-left: 40px;
  }

  .slogan {
    margin-bottom: 15px;
    font-size: 16px;
  }

  .box-1 {
    margin-right: 0;
    flex-grow: 4;
  }

  .box-2 {
    display: none;
  }

  .box-3 {
    width: 0;
  }

  .burger {
    margin-right: 100px;
  }

  .social {
    flex-grow: 11;
    margin-right: 56px;
  }

  .social img {
    margin-bottom: 30px;
  }
}